import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Roles } from 'Shared/constants/User'
import { logout, loggedIn } from 'Shared/ducks/user'
import { fetchUserIfNeeded } from 'Shared/ducks/user/candidate'
import ImpersonateUtility from 'Shared/components/ImpersonateUtility'

const mapStateToProps = state => ({
  user: state.user,
  role: Roles.candidate.name,
  redirectPath: Roles.candidate.impersonationRedirectPath
})

const mapDispatchToProps = dispatch => ({
  loggedIn: data => dispatch(loggedIn(data)),
  logout: () => dispatch(logout()),
  fetchUserById: id => dispatch(fetchUserIfNeeded(id))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ImpersonateUtility))



